$back-color-l: #EEFBFB;
$back-color-g: linear-gradient(to right,#05A5FA,#63FBD7);
$text-e-color:#3BA6C9;
$text-color: #333333;
@use './mixin' as m;


body {
    font-family: 'Noto Sans JP', sans-serif;
    color: $text-color;
}
.text-english {
    text-align: center;
    margin-bottom: 65px;
    color: $text-e-color;
}


.top {
    background-image: url(../img/circle/circle.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top -150px right -150px;

    &-inner {
        max-width: 1022px;
        height: 674px;
        margin: 0 auto;
        padding: 100px 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include m.tab {
            display: block;
            margin: 0 auto;
            padding-top: 200px;
            padding-bottom: 0;
        }
    }
}

.top-text {
    h2{
        font-size: 2rem; 
        line-height: 1.8;

        @include m.tab {
            font-size: 1.5rem;
            text-align: center;
        }
    }
    p {
        padding-top: 38px;
        padding-bottom: 38px;
        line-height: 1.8;

        @include m.tab {
            text-align: center;
        }
    }
}


@include m.tab {

    .top-button {
        display: none;
    }
}


.top-img {
    width: 518px;
    max-height: 430px;

    img {
        width: 100%;
        object-fit: contain;
    }

    @include m.tab {
        margin: 0 auto;
        width: 80%;
    }
}


.feature {

    &-inner {
        max-width: 1022px;
        height: 100%;
        margin: 0 auto;
        padding: 100px 20px;

        @include m.sp {
            padding-top: 0;
            padding-bottom: 0;
        }

        h2 {
            text-align: center;
            font-size: 2rem;
            margin-bottom: 20px;

            @include m.sp {
                font-size: 1.5rem;
            }
        }
        
        h3 {
            text-align: center;
            font-size: 1.75rem;
            margin-bottom: 54px;

            @include m.sp {
                font-size: 1.3rem;
            }
        }
    }

    &-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 93px;

        @include m.tab {
            display: block;
        }
    }

    &-img {
        width: 380px;
        height: 314px;

        img {
        width: 100%;
        }

        @include m.tab {
            margin: 0 auto;
            max-width: 100%;
        }
    }

    &-text {

        h4 {
            font-size: 1.5rem;
            line-height: 1.8;
            margin-bottom: 54px;
            padding: 20px;

            @include m.tab {
                font-size: 1.2rem;
                text-align: center;
            }
        }

        p {
            line-height: 1.8;
            padding: 20px;

            @include m.tab {
                text-align: center;
            }
        }
    }

    &-wrap--reverse {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        margin-bottom: 93px;

        @include m.tab {
            display: block;
        }
    }
}

.price {
    background-color: $back-color-l;

    &-inner {
        max-width: 1022px;
        height: 100%;
        margin: 0 auto;
        padding: 100px 20px;

        h2 {
            text-align: center;
            font-size: 2rem;
            margin-bottom: 23px;
            color: $text-color;

            @include m.tab {
                font-size: 1.5rem;
            }
        }
    }

    &-table {
        max-width: 926px;
        height: 410px;
        margin: 0 auto;
        margin-bottom: 41px;
        border: 1px solid #E5E5E5;
        background-color: #fff;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16);

    }

    &-title {
        width: 290px;
        height: 102px;
        padding-left: 20px;
        vertical-align: middle;
        border: 1px solid #E5E5E5;

        @include m.tab {
            font-size: 0.75rem;
            min-width: 200px;
        }

    }

    &-detail {
        width: 614px;
        padding-left: 30px;
        vertical-align: middle;
        border: 1px solid #E5E5E5;

        @include m.tab {
            font-size: 0.75rem;
        }
    }

    &-text {
        text-align: center;
        color: $text-color;
    }

}

.faq {

    &-inner {
        max-width: 1022px;
        height: 100%;
        margin: 0 auto;
        padding: 100px 20px;

        h2 {
            font-size: 2rem;
            text-align: center;
            margin-bottom: 23px;

            @include m.tab{
                font-size: 1.5rem;
            }
        }
    }
}

.accordion {
    max-width: 907px;
    margin: 0 auto;

    &-button {
        display: block;
        width: 100%;
        padding: 22px 10px 22px 10px;
        text-align: center;
        border: 1px solid #E5E5E5;

        @include m.tab {
            font-size: 0.75rem;
        }

    }

    &-text {
        padding: 28px 10px 28px 35px;
        text-align: left;
        font-size: 0.875rem;
        border: 1px solid #E5E5E5;
        background-color: #F2F2F2;
        
        @include m.tab {
            font-size: 0.75rem;
        }
    }

    &-title {
        cursor: pointer;
    }

    &-body {
        display: none;
    }
    
}

.blog {
    background-color: $back-color-l;

    &-inner {
        max-width: 1300px;
        height: 100%;
        padding: 100px 20px;
        margin: 0 auto;

        h2 {
            font-size: 2rem;
            text-align: center;
            margin-bottom: 23px;

            @include m.tab {
                font-size: 1.5rem;
            }
        }
    }

    &-img {

        img {
            width: 389px;
            height: 260px;

            @include m.tab {
                max-width: 100%;
            }
        }
    }

    &-wrap {
        width: 100%;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin-bottom: -30px;
        // マージン相殺
    }

    &-card {
        width: 389px;
        height: 387px;
        background-color: #fff;
        position: relative;
        margin-right: 30px;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16);

        // メディアクエリ時０
        margin-bottom: 30px;
        // マージン相殺

        @include m.tab {
            max-width: 100%;
            margin-right: 0;
        }
    }

    &-card:nth-of-type(3n) {
        margin-right: 0;
        // ３の倍数のカードマージン調整

    }


    &-text {
        margin-top: 19px;
        margin-left: 18px;
    }

    &-date {
        position: absolute;
        bottom: 11px;
        right: 38px;
    }

}

.works {

    &-inner {
        max-width: 100%;
        height: 100%;
        padding: 100px 20px;
        margin: 0 auto;
        overflow: hidden;

        h2 {
            font-size: 2rem;
            text-align: center;
            margin-bottom: 23px;
        }
    }

    &-img {
        width: 322px;

        img {
            width: 322px;
            height: 210px;
        }
    }



    &-text {
        margin-top: 30px;
        margin-left: 18px;

        p {
            margin-bottom: 34px;
        }

        dl {
            display: flex;
            flex-wrap: wrap;

            dt {
                width: 25%;
                padding-bottom: 10px;
                font-size: 0.875rem;
            }

            dd {
                width: 75%;
                padding-bottom: 10px;
                font-size: 0.875rem;

            }

            .title--long {
                width: 35%;
            }
            .text--short {
                width: 65%;
            }
        }
    }

}

.swiper-container {
width: 322px;
height: 407px;
margin: 0 auto;
}


.swiper-slide {
    width: 322px;
    height: 407px;
    background-color: #fff;
    margin-bottom: 30px;
    margin-right: 28px;
    margin-left: 28px;
    border: 1px solid #DDDDDD;
    border-radius: 10px;
    // マージン相殺
}