$text-color: #333333;
@use './mixin' as m;


.header {
    width: 100%;
    height: 94px;
    position: fixed;
    top: 0;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    background-color: #fff;
    z-index: 999;

&-inner {
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
}

&-logo {
    display: flex;
    align-items: center;
    img{
        width: 55px;
        height: 55px;
    }
}

&-title {
    font-size: 1.5rem;
    margin-left: 16px;
    color: $text-color;

    @include m.tab{
        font-size: 1rem;
    }
}

&__nav {

    ul {
        display: flex;
        justify-content: space-between;
        
        li {
            margin-right: 45px;

            a {
                color: $text-color;
            }
        }
    }

}


@include m.tab {

    .header__nav {
        position: absolute;
        right: 0;
        left: 0;
        top: 0;
        width: 100%;
        height: 100vh;
        transform: translateX(100%); 
        background-color: #fff; /*ハンバーガーメニュークリック時のナビゲーションメニュー背景色*/
        transition: ease .4s; /*ハンバーガーメニュークリック時のナビゲーションメニュー出現を遅延*/
    }
    .nav-items {
        margin-right: 0;
        display: block;
        padding: 0;
        position: absolute;
        top: 50%;
        left: 55%;
        transform: translate(-50%,-50%);
    }
    
      /* ナビのリンク */
    .nav-items__item a {
        color: black;
        width: 100%;
        display: block;
        text-align: center;
        font-size: 20px;
        margin-bottom: 24px;
    }
    
    .nav-items__item:last-child a {
        margin-bottom: 0;
    }
}

&-hamburger {
    display: none;
    width: 48px;
    height: 100%;

    @include m.tab {
        display: block;
    }
}
}

.hamburger {
    background-color: transparent; /*buttonタグデフォルトスタイルを打ち消し*/
    border-color: transparent; /*buttonタグデフォルトスタイルを打ち消し*/
    z-index: 9999;
}

.hamburger span {
    width: 100%;
    height: 5px;
    border-radius: 3px;
    background-color: rgb(72, 205, 212);
    position: relative;
    transition: ease .4s; /*ハンバーガーメニュークリック時の三本線の動きを遅延*/
    display: block;
}

.hamburger span:nth-child(1) {
    top: 0;
}  
.hamburger span:nth-child(2) {
    margin: 8px 0;
}

.hamburger span:nth-child(3) {
    top: 0;
}

// ハンバーガーメニュークリック後のスタイル
.header__nav.active {
transform: translateX(0);
}

.hamburger.active span:nth-child(1) {
top: 12px;
transform: rotate(45deg);
}

.hamburger.active span:nth-child(2) {
opacity: 0;
}

.hamburger.active span:nth-child(3) {
top: -13px;
transform: rotate(-45deg);
}

@include m.sp {
    
    .header-button {
        display: none;
    }
}