$back-color-g: linear-gradient(to right,#05A5FA,#63FBD7);
$back-color-l: #EEFBFB;
@use './mixin' as m;


.contact{
    background: $back-color-g;

    &-inner{
        max-width: 1022px;
        padding: 100px 20px;
        margin: 0 auto;

        h2{
            text-align: center;
            font-size: 2rem;
            color: #fff;
            margin-bottom: 19px;

            @include m.tab{
                font-size: 1.5rem;
            }
        }

        p{
            text-align: center;
            margin-bottom: 42px;
            color: #fff;
            line-height: 1.9;
        }

    }

    &-wrap{
        max-width: 908px;
        margin: 0 auto;
        background-color: #fff;
        padding: 35px 20px;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    }

    &-item{
        margin-left: 42px;
        margin-bottom: 32px;

    }

    &-title{
        display: block;
        margin-bottom: 42px;
    }

    &-accept{
        margin-top: 32px;
        text-align: center;
    }

    .contact-button{
        margin: 0 auto;
        margin-top: 33px;

    }
}

.end-message {
    display: none;
    color: #333333;
    
}

.false-message {
    display: none;
    color: #333333;
    
}

.input-field {
    max-width: 722px;
}

#username,#email {
    box-sizing: border-box;
    background-color: $back-color-l;
    width: 70%;
    height: 43px;
    border: none;
    border-radius: 3px;
}

#text {
    box-sizing: border-box;
    background-color: $back-color-l;
    width: 100%;
    height: 179px;
    border: none;
    border-radius: 3px;
}

.label-required {
    background-color: #0C449B;
    color: #fff;
    font-size: 0.875rem;
    padding: 8px 16px;
    border-radius: 10px;
    margin-left: 22px;
}

.box {
    width: 20px;
    height: 20px;
    border-color: #707070;
    margin-right: 10px;
}
