$button-color: linear-gradient(to right, rgb(250, 65, 204), rgb(96, 32, 176));

.contact-button {
    border: none;
    display: block;
    width: 155px;
    height: 37px;
    background: $button-color;
    color: #fff;
    text-align: center;
    border-radius: 10px;
    line-height: 37px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
}
