@use './mixin' as m;


.footer {
    
    background-color: #F8F8F8;

    &-inner {
        max-width: 1022px;
        height: 144px;
        margin: 0 auto;
        padding: 50px 20px;

    }

    &-title {
        text-align: center;
        font-size: 1.5rem;
    }

    &-wrap {
        display: flex;
        justify-content: center;
        margin-top: 30px;
        margin-bottom: 59px;

        @include m.sp{
            display: block;
            text-align: center;
            line-height: 1.8;
        }

    }

    &-text {
        margin-right: 30px;
    }
}

.small-text {
    text-align: center;
}